export function patch(target, source, options = {}) {
  const {
    key = 'id',
    updatePK,
    action
  } = options
  if (!updatePK) {
    if (!Array.isArray(source)) {
      const index = target.findIndex(vo => vo[key] === source[key])
      if (index > -1) {
        if (action === 'remove') {
          target.splice(index, 1)
        } else {
          target.splice(index, 1, source)
        }
      } else {
        target.push(source)
      }
    } else {
      target.forEach((vo, index) => {
        if (!source.find(item => item[key] === vo[key])) {
          target.splice(index, 1)
        }
      })
      source.forEach(vo => {
        if (!target.find(item => item[key] === vo[key])) {
          target.push(vo)
        }
      })
    }
  } else {
    const updateIndex = target.findIndex(item => item[key] === updatePK)
    const updateItem = source.find(item => item[key] === updatePK)
    if (updateIndex && updateItem) {
      target.splice(updateIndex, 1, updateItem)
    }
  }
}
