<template>
<section class="subpage--main flexrow">
  <Card class="tree-menu">
    <Tree
      ref="menuTree"
      :data="baseData"
      editable
      moveable
      @on-add-node="addNode"
      @on-edit-node="editNode"
      @on-remove-node="removeNode"
      @on-move-node="moveNode"
    />
  </Card>
  <Card class="flex__item pane--right">
    <div class="form-wrapper">
      <h1 class="title">{{isAddMode ? '新增节点' : '修改节点'}}</h1>
      <Form ref="form" :model="form" :rules="ruleValidate" :label-width="120">
        <FormItem label="资源ID" prop="id" v-show="false">
          <Input v-model="form.id" />
        </FormItem>
        <FormItem label="资源名称" prop="name">
          <Input v-model="form.name" />
        </FormItem>
        <FormItem label="资源编号" prop="code">
          <Input v-model="form.code" />
        </FormItem>
        <FormItem label="链接地址" prop="url">
          <Input v-model="form.url" />
        </FormItem>
        <FormItem v-show="false" label="上级资源" prop="parentId">
          <Input v-model="form.parentId" :disabled="true"/>
        </FormItem>
        <FormItem label="上级资源名称" prop="parentName">
          <Input v-model="form.parentName" :disabled="true"/>
          <Button type="primary" @click="isResourceSelectModalShown = true">打开上级资源选择框</Button>
          <Button type="primary" @click="toTop">设为顶级资源</Button>
        </FormItem>
        <FormItem label="节点类型" prop="type">
          <RadioGroup v-model="form.type">
            <Radio label="1">文件夹</Radio>
            <Radio label="0">子节点</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="使用状态" prop="state">
          <RadioGroup v-model="form.state">
            <Radio label="1">启用</Radio>
            <Radio label="0">停用</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="备注" prop="description">
          <Input v-model="form.description" type="textarea" :autosize="{minRows: 2, maxRows: 5}"/>
        </FormItem>
        <FormItem class="form-handler">
          <Button type="primary" @click="submit">提交</Button>
          <Button type="ghost" @click="handleReset('form')">重置</Button>
        </FormItem>
      </Form>
    </div>
  </Card>
  <Modal v-model="isResourceSelectModalShown" title="资源授权">
    <Tree
      ref="resourceTree"
      :data="baseData"
      style="margin: 50px"
      @on-select-change="selectParentNode"
    />
    <div slot="footer" class="modal-footer">
      <Button type="success" long @click="saveResource">保存</Button>
    </div>
  </Modal>
</section>
</template>
<script>
import Tree from '@/components/Tree'
import {
  padValue,
  setValue
} from '@/utils/object'
import {
  patch
} from '@/utils/array'

export default {
  components: {
    Tree
  },
  data() {
    return {
      form: {
        id: null,
        name: null,
        code: null,
        url: null,
        parentId: null,
        parentName: null,
        description: null,
        state: '1',
        isFolder: '0'
      },
      ruleValidate: {
        name: [{
          required: true,
          message: '资源名称不能为空',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          message: '资源编号不能为空',
          trigger: 'blur'
        }],
        state: [{
          required: true,
          message: '必须选择状态',
          trigger: 'blur'
        }]
      },
      baseData: [],
      currentNode: null,
      currentParentNode: null,
      isResourceSelectModalShown: false,
    };
  },
  computed: {
    isAddMode() {
      return this.form.id === null
    }
  },
  async created() {
    this.baseData = await this.$api.system.getResourceTree.send()
  },
  methods: {
    toTop() {
      Object.assign(this.form, {
        parentId: '0',
        parentName: null
      })
    },
    submit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          const methodName = this.isAddMode ? 'createResource' : 'updateResource'
          try {
            const data = await this.$api.system[methodName].send(this.form)
            const {
              selected,
              checked,
              expand
            } = this.currentNode
            patch(this.baseData, Object.assign(data, {
              selected,
              checked,
              expand
            }))
            padValue(this.form, null, ['state'])
            this.$Message.success('提交成功！')
          } catch (e) {
            this.$Message.error('提交失败！' + e.message)
          }
        } else {
          this.$Message.error('请填写必填项！')
        }
      })
    },
    async moveNode(node) {
      try {
        const data = await this.$api.system.updateResource.send(node)
        patch(this.baseData, Object.assign(data, {
          selected: false,
          checked: false
        }))
        this.$Message.success('成功移动节点！')
      } catch (e) {
        this.$Message.error('提交失败！' + e.message)
      }
    },
    handleReset(name) {
      this.$refs[name].resetFields()
    },
    addNode({
      node,
      isFolder
    }) {
      if (parseInt(node.type) === 1) {
        padValue(this.form, null, ['state', 'type'])
        Object.assign(this.form, {
          parentId: node.id,
          parentName: node.name,
          state: '1',
          type: isFolder ? '1' : '0'
        })
      } else {
        this.$Notice.error({
          title: '系统提示',
          desc: '此节点不能增加子节点！',
          duration: 2
        })
      }
    },
    async editNode(node) {
      padValue(this.form, null)
      try {
        const data = await this.$api.system.getResource.send({
          id: node.id
        })
        this.currentNode = node
        setValue(this.form, data)
      } catch (e) {}
    },
    async removeNode(node) {
      try {
        await this.$api.system.deleteResource.send({
          id: node.id
        })
        this.$Message.success('删除成功！')
        padValue(this.form)
        patch(this.baseData, node, {
          action: 'remove'
        })
      } catch (e) {
        this.$Message.error('删除失败,' + e.message)
      }
    },
    selectParentNode(nodes) {
      let node = nodes[0]
      if (parseInt(node.type) === 1) {
        this.currentParentNode = node
      } else {
        this.currentParentNode = null
        this.$Message.error('不能选择子节点作为上级资源！')
      }
    },
    saveResource() {
      this.isResourceSelectModalShown = false
      if (this.currentParentNode) {
        Object.assign(this.form, {
          parentId: this.currentParentNode.id,
          parentName: this.currentParentNode.name
        })
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.tree-menu {
  overflow-y: auto;
  min-width: 350px;
}
.pane--right {
  overflow-y: auto;
  margin-left: 10px;
}
</style>
